html {
  background-color: #adadad;
}
body {
  background-color: @white;
    width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    position: relative;
    @media (min-width:@tablet) {
      width: @tablet;
    }
    @media (min-width:@desktop) {
      width: @desktop;
    }
  }
.visyally-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
